import React, { createContext, useContext, useState } from "react";

const cartContext = createContext();

const CartContextProvider = ({ children }) => {
  const [test, setTest] = useState("O Agrotis");
  const [thecart, setThecart] = useState([]);
  const [openCart, setOpenCart] = useState(false);
  const [isCheckout, setIsCheckout] = useState(false);
  const [deliveryCost, setDeliveryCost] = useState(false);

  let bill = 0;
  const addOne = () => {
    setTest("I Agrotissa");
  };
  const cartLength = (key) => {
    return key;
  };
  const goToCheckout = () => {
    setIsCheckout(true);
    let body = document.getElementsByTagName("body")[0];
    body.classList.remove("opened-cart");
    window.scrollTo(0, 0);
  };
  const billTotal = () => {
    bill = 0;
    for (let i = 0; i < thecart.length; i++) {
      bill =
        bill + thecart[i].posotita * Number(thecart[i].price.replace(",", "."));
    }
    if (bill < 20 && bill > 0) {
      bill = bill + 5;
    }
    return bill;
  };
  const billWithoutDeliveryCost = () => {
    bill = 0;
    for (let i = 0; i < thecart.length; i++) {
      bill =
        bill + thecart[i].posotita * Number(thecart[i].price.replace(",", "."));
    }
    return bill;
  };
  const delivery = () => {
    bill = 0;
    for (let i = 0; i < thecart.length; i++) {
      bill =
        bill + thecart[i].posotita * Number(thecart[i].price.replace(",", "."));
    }
    if (bill > 19.9999) {
      return "Δωρεάν";
    } else {
      return "5€";
    }
  };
  const values = {
    test,
    addOne,
    cartLength,
    thecart,
    setThecart,
    openCart,
    setOpenCart,
    billWithoutDeliveryCost,
    billTotal,
    isCheckout,
    setIsCheckout,
    goToCheckout,
    delivery,
  };
  return <cartContext.Provider value={values}>{children}</cartContext.Provider>;
};

export const useCart = () => {
  return useContext(cartContext);
};

export default CartContextProvider;
