import React, { useState, useEffect } from "react";
import "../styles/Variables.scss";
import "../styles/DeliveryDisclaimer.scss";
import "bootstrap-icons/font/bootstrap-icons.css";
import { useCart } from "../context/CartContext";

const DeliveryDisclaimer = () => {
  const { billWithoutDeliveryCost } = useCart();
  const [costif, setCostIf] = useState(20);
  useEffect(() => {
    if (billWithoutDeliveryCost() > 20) {
      console.log("free");
    } else if (billWithoutDeliveryCost() > 0) {
      setCostIf(20 - billWithoutDeliveryCost());
      console.log("free if" + costif);
    } else {
      console.log("bill is zero");
    }
  }, [billWithoutDeliveryCost()]);

  return (
    <div className="delivery-disclaimer-wrapper">
      <div className="delivery-disclaimer-container">
        {billWithoutDeliveryCost() == 0 && (
          <div className="delivery-disclaimer-free-delivery-cost">
            Για όλες τις παραγγελίες άνω των 20&euro; τα μεταφορικά είναι
            δωρεάν.
          </div>
        )}
        {billWithoutDeliveryCost() > 19.9999 && (
          <div className="delivery-disclaimer-win-delivery-msg">
            <b>Κέρδισες δωρεάν μεταφορικά με αυτή τη παραγγελία.</b>
          </div>
        )}
        {billWithoutDeliveryCost() > 0 && billWithoutDeliveryCost() < 20 && (
          <div className="delivery-disclaimer-second-message">
            Έχεις χρεωθεί 5&euro; μεταφορικά.
            <br />
            <b>Προσθέστε προϊόντα αξίας {costif}&euro;</b> για να κερδίσετε
            δωρεάν μεταφορικά.
          </div>
        )}
      </div>
    </div>
  );
};

export default DeliveryDisclaimer;
