import React, { useState, useEffect } from "react";
import "../styles/Variables.scss";
import "../styles/Orders.scss";
import firebase from "./firebase";

const Orders = () => {
  const [orders, setOrders] = useState([]);
  var [allOrdersFlag, setAllOrdersFlag] = useState(false);
  var allOrders = [];
  var [isLoggedIn, setIsLoggedIn] = useState(false);

  const retrieveAllOrders = () => {
    if (isLoggedIn) {
      firebase
        .database()
        .ref("orders")
        .get()
        .then((snapshot) => {
          if (snapshot.exists()) {
            setOrders(snapshot.val());
            setAllOrdersFlag(true);
          } else {
            console.log("No data available");
          }
        })
        .catch((error) => {
          console.error(error);
        });
    }
  };

  useEffect(() => {
    /*authentication start */
    if (isLoggedIn) {
      retrieveAllOrders();
    } else {
      let yourpass = prompt("Please enter your pass");

      if (yourpass != null) {
        var email = "thanasis1914@yahoo.gr";

        //Sign In User with Email and Password
        firebase
          .auth()
          .signInWithEmailAndPassword(email, yourpass)
          .then(function () {
            console.log("success");
            window.isLoggedIn = true;
            setIsLoggedIn(true);
            retrieveAllOrders();
          })
          .catch(function (error) {
            // Handle Errors here.
            var errorCode = error.code;
            var errorMessage = error.message;
            console.log(errorCode);
            console.log(errorMessage);
            window.isLoggedIn = false;
            setIsLoggedIn(false);
            let yourpass = prompt("Please enter your pass");
          });
      }
    }
    /*authentication end */
  }, [isLoggedIn, orders]);

  const deleteOrder = (id) => {
    //console.log(id.id)
    firebase
      .database()
      .ref("orders/" + id.id)
      .remove();
    retrieveAllOrders();
  };
  return (
    isLoggedIn && (
      <div className="orders-wrapper">
        <div className="order-title">Παραγγελίες</div>
        {allOrdersFlag &&
          Object.keys(orders).forEach(function (key) {
            //console.log("key: " + key);
            //console.log(orders[key]);
            allOrders.push({
              item: orders[key],
              id: key,
            });
          })}
        {allOrders.length > 0 ? (
          allOrders.map(({ item, id }) => {
            return (
              <div className="order-item" key={id}>
                <div className="customer-info-container">
                  <div>
                    {item.checkoutInfo.firstName} {item.checkoutInfo.lastName}
                  </div>
                  <div>{item.checkoutInfo.mobile}</div>
                  <div>{item.checkoutInfo.email}</div>
                  <div>
                    {item.checkoutInfo.city} - {item.checkoutInfo.zip}
                  </div>
                  <div>{item.checkoutInfo.address}</div>
                  <div>{item.statusOrder}</div>
                  <div>{item.checkoutInfo.deliveryOption}</div>
                  <div>{item.checkoutInfo.paymentOption}</div>
                  <div>
                    {item.hour} - {item.date}
                  </div>
                </div>
                <div className="procucts-for-order">
                  {item.products.map(
                    ({ id, price, posotita, title, quantity }, index) => {
                      return (
                        <div key={id}>
                          {posotita} - {title} ({quantity}) | {price}&euro;
                        </div>
                      );
                    }
                  )}
                  <div>Σύνολο Παραγγελίας: {item.totalPrice}&euro;</div>
                </div>
                <div className="orders-item-buttons">
                  <div
                    className="delete-this-order"
                    onClick={(e) => deleteOrder({ id })}
                  >
                    Διαγραφή Παραγγελίας <i className="bi bi-x-circle"></i>
                  </div>
                </div>
              </div>
            );
          })
        ) : (
          <div>Δεν υπάρχουνε διαθέσιμες παραγγελίες.</div>
        )}
      </div>
    )
  );
};

export default Orders;
