import React from "react";
import "../styles/Variables.scss";
import "../styles/Product.scss";
import "bootstrap-icons/font/bootstrap-icons.css";

const Product = ({ data, handleClick, handleMinus, handlePlus }) => {
  let id = data.id;
  let title = data.title;
  let description = data.description;
  let price = data.price;
  let initprice = data.initprice;
  let quantity = data.quantity;
  let posotita = data.posotita;
  let image = data.image;
  return (
    <div className="product">
      {image.length > 0 ? (
        <div className="image-prod">
          <img src={`img/${image}`} />
          {/*add image in folder public/img/ */}
        </div>
      ) : null}
      <div className="product-inner">
        <div className="firstcol">
          <div className="title">{title}</div>
        </div>
        <div className="secondcol">
          <div className="fullPrice">
            {initprice.length > 0 ? (
              <div className="initPrice">{initprice}&euro;</div>
            ) : null}
            <div className="price">{price}&euro;</div>
          </div>
        </div>
        <div className="thirdcol">
          {posotita === 0 && (
            <div onClick={() => handleClick({ id, title, price, posotita })}>
              <i className="bi bi-plus-square-fill"></i>
            </div>
          )}
          {posotita === 1 && (
            <div className="change_quantity">
              <span onClick={() => handleMinus({ id, title, price, posotita })}>
                <i className="bi bi-dash-square-dotted"></i>
              </span>
              <span className="quantity">{posotita}</span>
              <span onClick={() => handlePlus({ id, title, price, posotita })}>
                <i className="bi bi-plus-square"></i>
              </span>
            </div>
          )}
          {posotita !== 0 && posotita !== 1 && (
            <div className="change_quantity">
              <span onClick={() => handleMinus({ id, title, price, posotita })}>
                <i className="bi bi-dash-square"></i>
              </span>
              <span className="quantity">{posotita}</span>
              <span onClick={() => handlePlus({ id, title, price, posotita })}>
                <i className="bi bi-plus-square"></i>
              </span>
            </div>
          )}
        </div>
        <div className="description">
          {description} ({quantity}){" "}
        </div>
      </div>
    </div>
  );
};

export default Product;
