import React from "react";
import "../styles/Variables.scss";
import "../styles/Hero.scss";
import heroImg from "../images/localprods_market_nuts.jpg";

const Hero = () => {
  return (
    <div className="hero-wrapper">
      <div className="hero-container">
        <img src={heroImg} />
      </div>
    </div>
  );
};

export default Hero;
